import React from "react";
import CardSection from "@src/components/Card/CardSection";
import { cardInfo } from "@src/interfaces/common";
import '../styles/css/Education.scss'

const educationList: cardInfo[] = [
  {
    title: "RISE Krishna Sai Prakasam Group Institutions",
    subTitle: "B.Tech in Computer Science",
    duration: "June 2017 - Aug 2021",
    details: ["GPA: 7.73/10.0"],
  },
  {
    title: "Narayana Jr College, Kadapa",
    subTitle: "Intermediate",
    duration: "2015 - 2017",
    details: ["GPA: 93.3/100"],
  },
  {
    title: "Vijaya High School, Porumamilla",
    subTitle: "SSC",
    duration: "2015",
    details: ["GPA: 87/100"],
  },
];

const Education: React.FC = () => {
  return (
    <div
      className="education-container"
      style={{padding: "2.5rem",}}
    >
      <CardSection
        title="Educational Background"
        subtitle="Highlights of Academic Achievements"
        cardInfoList={educationList}
        borderColor="#ff9800"
      />
    </div>
  );
};

export default Education;
